import { ResetPasswordResponse } from '@/auth/types';
import { ResetPasswordModel } from '../models';

export const transformResetPassword = (
  response: ResetPasswordResponse
): ResetPasswordModel => {
  let message: string = '';

  if (response?.errors?.length)
    message = response?.errors[0]?.message as unknown as string;

  return {
    message,
    success: Boolean(response?.data?.resetPassword),
  };
};
